import React, {useState} from 'react';

export default function Form() {
    const handleSubmit = (e) => {
        e.preventDefault();
        console.log('Form was just submitted');
        // console.log(vehicleId, startYear, endYear, startMonth, endMonth, startDay, endDay);
        const date = createDate(startYear, endYear, startMonth, endMonth, startDay, endDay);
        const time = [startTime,endTime];

        const reqBody = {
            vehicleId:vehicleId.split(','),
            date,
            time
        }

        console.log(reqBody)
        makeQuery(reqBody)
    }

    const makeQuery = async dataRqst => {
        const body = JSON.stringify({dataRqst});
        console.log(body);
        try {
            const response = await fetch("http://localhost:5000/api", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body:body
        });            
            console.log(response);
        } catch (error) {
            console.log(error.message);
        }
    }

    const createDate = (sy,ey,sm,em,sd,ed)=>{
        const startDate = `${sd}/${sm}/${sy}`;
        const endDate = `${ed}/${em}/${ey}`;
        return [startDate, endDate];
    }

    const [vehicleId, setvehicleId] = useState('');
    const [startYear, setstartYear] = useState('');
    const [endMonth, setendMonth] = useState('');
    const [endYear, setendYear] = useState('');
    const [startMonth, setstartMonth] = useState('');
    const [startDay, setstartDay] = useState('');
    const [endDay, setendDay] = useState('');
    const [startTime, setstartTime] = useState('');
    const [endTime, setendTime] = useState('');   

    return (
        <div className="input-form">
            <h2>Input Details</h2>
            <form  onSubmit={(e)=>handleSubmit(e)} className="form">
                <div className="date-duration">
                    <label htmlFor="date-duration" className="form-label">Select Day, Date, Time</label>
                    <p className="input-info">The vehicle data will be analyzed, averaged and graphed as per the specified day, month or year time interval. <br/> <br/> Eg : Between 1/2/2020 and 3/2/2020 </p>
                    <table>
                        <thead>
                            <tr>
                                <th>YEAR</th>
                                <th>MONTH</th>
                                <th>DAY</th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>
                                    <p>Enter the start and end year in YYYY format</p>
                                    <input onChange={(e)=>{setstartYear(e.target.value)}} value={startYear}  type="text" name="start-year" id="start-year" className="form-input" placeholder="Eg : 2019"/>
                                    <input onChange={(e)=>{setendYear(e.target.value)}} value={endYear}  type="text" name="end-year" id="end-year" className="form-input" placeholder="Eg : 2020"/>
                                </td>
                                <td>
                                    <p>Enter the start and end month in MM format</p>
                                    <input onChange={(e)=>{setstartMonth(e.target.value)}} value={startMonth}  type="text" name="start-month" id="start-month" className="form-input" placeholder="Eg : 02"/>
                                    <input onChange={(e)=>{setendMonth(e.target.value)}} value={endMonth}  type="text" name="end-month" id="end-month" className="form-input" placeholder="Eg : 03"/>
                                </td>
                                <td>
                                    <p>Enter the start and end day in DD format</p>
                                    <input onChange={(e)=>{setstartDay(e.target.value)}} value={startDay}  type="text" name="start-day" id="start-day" className="form-input" placeholder="Eg : 01"/>
                                    <input onChange={(e)=>{setendDay(e.target.value)}} value={endDay}  type="text" name="end-day" id="end-day" className="form-input" placeholder="Eg : 18"/>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div className="vehicle-id">
                    <label htmlFor="vehicle-id" className="form-label">Select Vehicle ID</label>
                    <p className="input-info">Enter an array of vehicle IDs <br/>Eg : EV20201, EV20202, EV20203 </p>
                    <input onChange={(e)=>{setvehicleId(e.target.value)}} value={vehicleId} required type="text" name="vehicle-id" id="vehicle-id" className="form-input" placeholder="Eg : EV20201, EV20202"/>
                </div>                
                <div className="time-duration">
                    <p className="form-label">Select Time Duration</p>
                    <div className="time-inputs">
                        <label htmlFor="start-time-input">Select Start Time</label>
                        <label htmlFor="end-time-input">Select End Time</label>
                        <input type="text" onChange={(e)=>{setstartTime(e.target.value)}} value={startTime} id="start-time-input" name="time-input" className="form-input" placeholder="13:01:00"/>
                        <input type="text" onChange={(e)=>{setendTime(e.target.value)}} value={endTime} id="end-time-input" name="time-input" className="form-input" placeholder="13:09:00"/>
                    </div>
                </div>
                <div className="submit-n-err">
                    <div className="error-box">This is where errors will be shown</div>        
                    <button type="submit" className="submit-btn">Submit</button>
                </div>
            </form>
        </div>
    )
}
