import React from 'react'
import ather from '../img/ather.png'
import steeringWheel from '../svg/steeringWheel.svg'

export default function Navbar() {
    return (
        <div className="navbar">
            <div className="logo">
                <img src={ather} alt="Ather"/>
            </div>
            <div className="brand">
                <div className="steering">
                    <img src={steeringWheel} alt="Steering wheel"/>
                </div>
                <span className="text dash">DASH</span>
                <span className="text">BOARD</span>
            </div>
            <div className="navLinks">
                <a href="/">Dash</a>
                <a href="/data">Data</a>
                <a href="/about">About</a>
            </div>
        </div>
    )
}
